<template>
  <v-container fluid class="down-top-padding">
    <v-row>
      <input type="file" name="" class="d-none"  ref="files" @change="fileInput" id="">
      <v-col cols="12" lg="4">
        <v-card>
          <v-card-text class="text-center pa-7">
            <v-list-item-avatar size="170" color="grey darken-3">
          <v-img
            v-if="colorShow"
            class="elevation-6"
            :src="host.image==''?'https://s3.amazonaws.com/37assets/svn/765-default-avatar.png':host.image"
             ></v-img>
        </v-list-item-avatar>
              <div class="text-center">
            <v-btn class="text-capitalize mt-5 element-0" style="color:white"  @click="$refs['files'].click()" color="danger"> + Resim {{host.images!='null'?'Düzenle':'Ekle'}}</v-btn>
  </div>
  <div class="text-center text danger" style="margin-top:10px;color:white">{{errImage}}</div>
            <h4 class="mt-2 title blue-grey--text text--darken-2 font-weight-regular">{{host.fullname}}</h4>
            <h6 class="subtitle-2 font-weight-light">{{$t('header.host')}}</h6>

            <div style="padding:10px">
            <v-btn class="text-capitalize mt-5 element-0" style="color:white"  @click="$router.push('/pages/list-host')" color="primary">{{$t('host.hostShow')}}</v-btn>
            </div>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="12" lg="8">
        <v-card>
          <v-card-text>
            <h3 class="title blue-grey--text text--darken-2 font-weight-regular">{{$t('dashboard.inserthost')}}</h3>
            <h6 class="subtitle-2 font-weight-light">{{$t('host.info')}}</h6>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-text>
            <v-text-field
              v-model="host.fullname"
              :error-messages="errName"
              :label="$t('host.fullname')"
              filled
              background-color="transparent"
            ></v-text-field>
            <v-text-field
              v-model="host.busName"
              :error-messages="errBusName"
              :label="$t('host.busName')"
              filled
              background-color="transparent"
            ></v-text-field>
               <v-text-field
              v-model="host.busLicense"
              :error-messages="errBusLicense"
               :label="$t('host.busLicense')"
              filled
              background-color="transparent"
            ></v-text-field>
            <v-text-field
              type="host.username"
              v-model="host.username"
             :label="$t('generate.username')"
              filled
              :success-messages="successmessages"
              :error-messages="errormessages"
              @blur="controlUsername"
              background-color="transparent"
            ></v-text-field>
            <v-text-field
              v-model="host.password"
              filled
              :error-messages="errPassword"
              background-color="transparent"
              :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
              :type="show1 ? 'text' : 'password'"
              name="input-10-1"
                :label="$t('generate.password')"
              :hint="$t('err.password')"
              counter
              @click:append="show1 = !show1"
            ></v-text-field>
           <v-text-field
            @blur="phoneControl()"
              type="host.phone"
              :error-messages="errPhone"
              v-model="host.phone"
             :label="$t('generate.phone')"
              filled
              background-color="transparent"
            ></v-text-field>

            <v-btn class="text-capitalize mt-5 element-0" :loading="loading" :disabled="loading" @click="sendhost()" color="success">{{$t('generate.submit')}}</v-btn>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import {phoneNumberControl} from '../../plugins/rules'
export default {
  name: "Profile",

  data: () => ({
    showList:false,
    errName:[],
    dialog:false,
    errLastname:[],
    errPhone:[],
    errPassword:[],
    errBusName:[],
    errBusLicense:[],
    errImage:"",
    host:{

      fullname:"",
      username:"",
      password:"",
      phone:"",
      file:[],
      busName:"",
      busLicense:"",
      image:"",
    },
    emailtext: "",
    password: "",
    disableinput: "",
    checkbox1: "",
    checkbox2: "",
    checkbox3: "",
    colorShow:true,
    show1: false,
    loading:false,
    errormessages:[],
    successmessages:[]
  }),
  methods:{
      phoneControl(){
    let result = phoneNumberControl(this.host.phone)
    if(result){
      this.errPhone.push(result)
    }
    else{
      this.errPhone=[]
    }
  },
      controlUsername(){
    this.loading=true
     if(this.host.username.length>3){
        this.$api.get('h/controlHostname?username="'+this.host.username+'"').then(res=>{
        if(res.count==0){
            this.successmessages=[]
                this.errormessages=[]
          this.successmessages.push('Bu kullanıcı adı kullanılabilir.')
           this.loading=false
        }
        else{
            this.successmessages=[]
          this.errormessages=[]
          this.errormessages.push(`${this.host.username} adlı kullanıcı ismi kullanılıyor.`)
           this.loading=false

        }
      })
     }
     else
        this.errormessages.push('Kullanıcı Adı 3 karakterden fazla olmalıdır.')
    this.loading=false

    },
   async sendhost(){
     this.errName=[]
     this.errLastname=[]
     this.errPassword=[]
     this.errBusName=[]
     this.errBusLicense=[]
     this.errImage=[]
      this.loading = true
    if(this.host.fullname==''||this.host.lastname==''||this.host.busLicense==''||this.host.busName==''||this.host.username==''||this.host.phone.length<9||this.errPhone.length!=0||this.host.password<5||this.host.file.length==0 || this.errormessages.length!=0){
      if(this.host.fullname==''){
        this.errName.push('Öğretmen Adı boş geçilemez.')
      }
      if(this.host.lastname==''){
        this.errLastname.push('Öğretmen Soyadı boş geçilemez.')
      }
       if(this.host.busName==''){
        this.errBusName.push('Öğretmen Adı boş geçilemez.')
      }
      if(this.host.busLicense==''){
        this.errBusLicense.push('Öğretmen Soyadı boş geçilemez.')
      }
      if(this.host.username==''){
        this.errormessages.push('Kullanıcı Adı Boş geçilemez.')
      }
      if(this.host.phone.length<9){
        this.errPhone.push('Eksik veya yanlış telefon numarası girdiniz.')
      }
      if(this.host.password.length<5){
        this.errPassword.push('Şifreniz en az 6 karakter olmalıdır.')
      }
       if(this.host.file.length==0){
        this.errImage = 'Öğretmenin resmini eklemelisiniz.'
      }
      this.loading = false
    }
    else{
      let fd = new FormData();
      fd.append('fullname',this.host.fullname)
      fd.append('phone',this.host.phone)
      fd.append('username',this.host.username)
      fd.append('password',this.host.password)
      fd.append('busName',this.host.busName)
      fd.append('busLicense',this.host.busLicense)
      fd.append('image',this.host.file)
      let result = await this.$api.postfd('host','image',fd)
      if(result.status=='success'){
       this.loading = false
        this.$router.push('/pages/list-host')
      }
      else{
     this.loading = false

      }
    }

    },
       createImage(file) {

      var reader = new FileReader();
      var vm = this;
      reader.onload = e => {
        vm.host.image = e.target.result;
             this.colorShow=true
      };
      reader.readAsDataURL(file);


    },
    fileInput(event){
      this.colorShow=false
       this.host.file = event.target.files[0]
       this.createImage(this.host.file)
    },
  }
};
</script>